import { Component, EventEmitter, Input, Output, signal, OnInit, OnChanges, SimpleChanges, AfterViewInit } from '@angular/core';
import { ReminderService } from '../../../services/reminder.service';
import { TranslationService } from '../../../services/translation.service';
import { Reminder } from '../../../models/reminder.model';

@Component({
  selector: 'app-all-reminders-modal',
  standalone: false,
  templateUrl: './all-reminders-modal.component.html',
  styleUrls: ['./all-reminders-modal.component.css']
})
export class AllRemindersModalComponent implements OnInit, OnChanges, AfterViewInit {
  @Input() isOpen = signal(false);
  @Output() closeModal = new EventEmitter<void>();
  @Output() reminderUpdated = new EventEmitter<void>();

  reminders = signal<Reminder[]>([]);
  loading = signal(false);
  error = signal<string | null>(null);
  selectedReminder = signal<Reminder | null>(null);
  showEditModal = signal(false);
  showDeleteConfirm = signal(false);
  reminderToDelete = signal<Reminder | null>(null);

  constructor(
    private reminderService: ReminderService,
    private translationService: TranslationService
  ) {}

  ngOnInit(): void {
    // Load reminders when component initializes
    this.loadReminders();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['isOpen'] && this.isOpen()) {
      this.loadReminders();
    }
  }

  // Also load reminders when the modal becomes visible
  ngAfterViewInit(): void {
    if (this.isOpen()) {
      this.loadReminders();
    }
  }

  // Add a method to be called when modal opens
  onModalOpen(): void {
    this.loadReminders();
  }

  loadReminders(): void {
    this.loading.set(true);
    this.error.set(null);

    this.reminderService.getMyReminders().subscribe({
      next: (response) => {
        this.loading.set(false);
        if (response.success && response.data) {
          const remindersArray = Array.isArray(response.data) ? response.data : [response.data];
          this.reminders.set(remindersArray);
        } else {
          this.reminders.set([]);
        }
      },
      error: (error) => {
        this.loading.set(false);
        console.error('Error loading reminders:', error);
        this.error.set(this.translate('reminders.load_error'));
      }
    });
  }

  onEditReminder(reminder: Reminder): void {
    this.selectedReminder.set(reminder);
    this.showEditModal.set(true);
  }

  onDeleteReminder(reminder: Reminder): void {
    this.reminderToDelete.set(reminder);
    this.showDeleteConfirm.set(true);
  }

  confirmDelete(): void {
    const reminder = this.reminderToDelete();
    if (reminder) {
      this.loading.set(true);
      this.reminderService.deleteReminder(reminder.id).subscribe({
        next: (response) => {
          this.loading.set(false);
          if (response.success) {
            this.reminders.update(reminders => 
              reminders.filter(r => r.id !== reminder.id)
            );
            this.reminderUpdated.emit();
            this.showDeleteConfirm.set(false);
            this.reminderToDelete.set(null);
          } else {
            this.error.set(response.message || this.translate('reminders.delete_error'));
          }
        },
        error: (error) => {
          this.loading.set(false);
          console.error('Error deleting reminder:', error);
          this.error.set(this.translate('reminders.delete_error'));
        }
      });
    }
  }

  cancelDelete(): void {
    this.showDeleteConfirm.set(false);
    this.reminderToDelete.set(null);
  }

  onReminderUpdated(): void {
    this.loadReminders();
    this.reminderUpdated.emit();
    this.showEditModal.set(false);
    this.selectedReminder.set(null);
  }

  // Public method to refresh reminders
  refreshReminders(): void {
    this.loadReminders();
  }

  onEditModalClose(): void {
    this.showEditModal.set(false);
    this.selectedReminder.set(null);
  }

  onClose(): void {
    this.closeModal.emit();
  }

  formatDateTime(dateString: string): string {
    const date = new Date(dateString);
    return date.toLocaleString();
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }
}
