import { Component, OnInit, OnDestroy, signal, ViewChild } from '@angular/core';
import { CalendarService } from '../../services/calendar.service';
import { TranslationService } from '../../services/translation.service';
import { LangService } from '../../services/lang.service';
import { ReminderService } from '../../services/reminder.service';
import { 
  CalendarEvent, 
  EventType, 
  EventStatus, 
  CalendarFilters 
} from '../../models/calendar.model';
import { Reminder } from '../../models/reminder.model';
import { Subscription } from 'rxjs';
import { AllRemindersModalComponent } from './all-reminders-modal/all-reminders-modal.component';

@Component({
  selector: 'app-calendar',
  standalone: false,
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.component.css']
})
export class CalendarComponent implements OnInit, OnDestroy {
  // Signals for reactive state management
  userEvents = signal<CalendarEvent[]>([]);
  systemEvents = signal<CalendarEvent[]>([]);
  filteredEvents = signal<CalendarEvent[]>([]);
  currentDate = signal<Date>(new Date());
  selectedDate = signal<Date | null>(null);
  loading = signal<boolean>(false);
  error = signal<string | null>(null);
  isRTL = signal<boolean>(false);

  // Filter state
  filters = signal<CalendarFilters>({});

  // Calendar view state
  viewMode = signal<'month' | 'week' | 'day'>('month');
  showAllUserEventsModal = signal<boolean>(false);

  // Calendar data
  calendarDays: Date[] = [];
  weekDays: string[] = [];
  monthNames: string[] = [];

  // Reminder management
  reminders = signal<Reminder[]>([]);
  showAddReminderModal = signal<boolean>(false);
  showAllRemindersModal = signal<boolean>(false);
  showDayRemindersModal = signal<boolean>(false);
  selectedDayReminders = signal<Reminder[]>([]);
  selectedDayDate = signal<Date | null>(null);

  @ViewChild('allRemindersModal') allRemindersModal!: AllRemindersModalComponent;

  private subscriptions: Subscription[] = [];

  constructor(
    private calendarService: CalendarService,
    private translationService: TranslationService,
    private langService: LangService,
    private reminderService: ReminderService
  ) {}

  ngOnInit(): void {
    this.initializeCalendar();
    this.loadEvents();
    this.loadReminders();
    this.setupLanguageSubscription();
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach(sub => sub.unsubscribe());
  }

  private initializeCalendar(): void {
    this.generateCalendarDays();
    this.setupWeekDays();
    this.setupMonthNames();
  }

  private setupLanguageSubscription(): void {
    const langSub = this.langService.currentLang$.subscribe((lang: 'ar' | 'en') => {
      this.isRTL.set(lang === 'ar');
      this.setupWeekDays();
      this.setupMonthNames();
    });
    this.subscriptions.push(langSub);
  }

  private setupWeekDays(): void {
    if (this.isRTL()) {
      this.weekDays = ['الأحد', 'الاثنين', 'الثلاثاء', 'الأربعاء', 'الخميس', 'الجمعة', 'السبت'];
    } else {
      this.weekDays = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
    }
  }

  private setupMonthNames(): void {
    if (this.isRTL()) {
      this.monthNames = [
        'يناير', 'فبراير', 'مارس', 'أبريل', 'مايو', 'يونيو',
        'يوليو', 'أغسطس', 'سبتمبر', 'أكتوبر', 'نوفمبر', 'ديسمبر'
      ];
    } else {
      this.monthNames = [
        'January', 'February', 'March', 'April', 'May', 'June',
        'July', 'August', 'September', 'October', 'November', 'December'
      ];
    }
  }

  private generateCalendarDays(): void {
    const current = this.currentDate();
    const year = current.getFullYear();
    const month = current.getMonth();
    const day = current.getDate();
    
    this.calendarDays = [];
    
    if (this.viewMode() === 'month') {
      // Get first day of month and calculate starting point
      const firstDay = new Date(year, month, 1);
      const lastDay = new Date(year, month + 1, 0);
      const startDate = new Date(firstDay);
      
      // Adjust start date to beginning of week
      const dayOfWeek = firstDay.getDay();
      startDate.setDate(startDate.getDate() - dayOfWeek);
      
      const endDate = new Date(lastDay);
      endDate.setDate(endDate.getDate() + (6 - lastDay.getDay()));
      
      for (let d = new Date(startDate); d <= endDate; d.setDate(d.getDate() + 1)) {
        this.calendarDays.push(new Date(d));
      }
    } else if (this.viewMode() === 'week') {
      // Get start of week (Sunday)
      const startOfWeek = new Date(current);
      const dayOfWeek = current.getDay();
      startOfWeek.setDate(current.getDate() - dayOfWeek);
      
      // Generate 7 days for the week
      for (let i = 0; i < 7; i++) {
        const day = new Date(startOfWeek);
        day.setDate(startOfWeek.getDate() + i);
        this.calendarDays.push(day);
      }
    } else if (this.viewMode() === 'day') {
      // Show only the selected day
      this.calendarDays.push(new Date(current));
    }
  }

  loadEvents(): void {
    this.loading.set(true);
    this.error.set(null);

    // Load only user events for My Calendar
    const userEventsSub = this.calendarService.getUserEvents().subscribe({
      next: (response) => {
        if (response.success) {
          this.userEvents.set(response.data);
          this.systemEvents.set([]);
          this.updateFilteredEvents();
        }
        this.loading.set(false);
      },
      error: (error) => {
        console.error('Error loading user events:', error);
        this.error.set('Failed to load user events');
        this.loading.set(false);
      }
    });
    this.subscriptions.push(userEventsSub);
  }

  private updateFilteredEvents(): void {
    let filtered = this.userEvents();

    // Apply type filter
    if (this.filters().type !== undefined) {
      filtered = filtered.filter(event => event.type === this.filters().type);
    }

    // Apply status filter
    if (this.filters().status !== undefined) {
      filtered = filtered.filter(event => event.status === this.filters().status);
    }

    this.filteredEvents.set(filtered);
  }

  // Navigation methods
  previousMonth(): void {
    const current = this.currentDate();
    current.setMonth(current.getMonth() - 1);
    this.currentDate.set(new Date(current));
    this.generateCalendarDays();
  }

  nextMonth(): void {
    const current = this.currentDate();
    current.setMonth(current.getMonth() + 1);
    this.currentDate.set(new Date(current));
    this.generateCalendarDays();
  }

  goToToday(): void {
    this.currentDate.set(new Date());
    this.generateCalendarDays();
  }

  // Event handling
  onDateClick(date: Date): void {
    this.selectedDate.set(date);
    
    // Check if this day has reminders
    const dayReminders = this.getRemindersForDate(date);
    if (dayReminders.length > 0) {
      this.selectedDayReminders.set(dayReminders);
      this.selectedDayDate.set(date);
      this.showDayRemindersModal.set(true);
    }
  }


  onFilterChange(filters: CalendarFilters): void {
    this.filters.set(filters);
    this.loadEvents();
  }

  onTypeFilterChange(value: string): void {
    const newFilters = { ...this.filters() };
    newFilters.type = value ? +value : undefined;
    this.onFilterChange(newFilters);
  }

  onStatusFilterChange(value: string): void {
    const newFilters = { ...this.filters() };
    newFilters.status = value ? +value : undefined;
    this.onFilterChange(newFilters);
  }

  retryLoadEvents(): void {
    this.loadEvents();
  }

  onViewModeChange(mode: 'month' | 'week' | 'day'): void {
    this.viewMode.set(mode);
    // Regenerate calendar days based on view mode
    this.generateCalendarDays();
  }


  viewAllUserEvents(event: Event): void {
    console.log('View All button clicked');
    
    // Prevent default behavior and stop propagation
    event.preventDefault();
    event.stopPropagation();
    
    // Open the modal to show all user events
    this.showAllUserEventsModal.set(true);
    
    console.log('All user events modal opened');
  }

  closeAllUserEventsModal(): void {
    this.showAllUserEventsModal.set(false);
  }


  // Helper methods
  getEventsForDate(date: Date): CalendarEvent[] {
    const dateStr = date.toISOString().split('T')[0];
    return this.filteredEvents().filter(event => {
      const eventDate = new Date(event.startDate).toISOString().split('T')[0];
      return eventDate === dateStr;
    });
  }

  getUserEventsForDate(date: Date): CalendarEvent[] {
    const dateStr = date.toISOString().split('T')[0];
    return this.userEvents().filter(event => {
      const eventDate = new Date(event.startDate).toISOString().split('T')[0];
      return eventDate === dateStr;
    });
  }


  isToday(date: Date): boolean {
    const today = new Date();
    return date.toDateString() === today.toDateString();
  }

  isSelectedDate(date: Date): boolean {
    const selected = this.selectedDate();
    return selected ? date.toDateString() === selected.toDateString() : false;
  }

  isCurrentMonth(date: Date): boolean {
    const current = this.currentDate();
    return date.getMonth() === current.getMonth() && date.getFullYear() === current.getFullYear();
  }

  getEventTypeLabel(type: EventType): string {
    const key = this.calendarService.getEventTypeLabel(type);
    return this.translate(key);
  }

  getEventStatusLabel(status: EventStatus): string {
    const key = this.calendarService.getEventStatusLabel(status);
    return this.translate(key);
  }

  getEventStatusColor(status: EventStatus): string {
    return this.calendarService.getEventStatusColor(status);
  }

  getPriorityColor(priority: number): string {
    return this.calendarService.getPriorityColor(priority);
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  // Enum getters for template
  get EventType() {
    return EventType;
  }

  get EventStatus() {
    return EventStatus;
  }

  // Reminder management methods
  loadReminders(): void {
    this.reminderService.getMyReminders().subscribe({
      next: (response) => {
        if (response.success && response.data) {
          const remindersArray = Array.isArray(response.data) ? response.data : [response.data];
          this.reminders.set(remindersArray);
        } else {
          this.reminders.set([]);
        }
      },
      error: (error) => {
        console.error('Error loading reminders:', error);
        this.reminders.set([]);
      }
    });
  }

  openAddReminderModal(): void {
    this.showAddReminderModal.set(true);
  }

  closeAddReminderModal(): void {
    this.showAddReminderModal.set(false);
  }

  onReminderCreated(): void {
    this.loadReminders();
  }

  openAllRemindersModal(): void {
    this.showAllRemindersModal.set(true);
    // Force reload reminders when opening the modal
    this.loadReminders();
    
    // Also refresh the modal's reminders after a short delay to ensure the modal is rendered
    setTimeout(() => {
      if (this.allRemindersModal) {
        this.allRemindersModal.refreshReminders();
      }
    }, 100);
  }

  closeAllRemindersModal(): void {
    this.showAllRemindersModal.set(false);
  }

  onReminderUpdated(): void {
    this.loadReminders();
  }

  getRemindersForDate(date: Date): Reminder[] {
    const dateStr = date.toISOString().split('T')[0];
    return this.reminders().filter(reminder => {
      const reminderDate = new Date(reminder.reminderTime).toISOString().split('T')[0];
      return reminderDate === dateStr;
    });
  }

  hasRemindersForDate(date: Date): boolean {
    return this.getRemindersForDate(date).length > 0;
  }

  getReminderColor(reminder: Reminder, index: number): string {
    // Use a single color for all reminders
    return '#FF3B3B'; // Red color
  }

  // Day reminders modal methods
  closeDayRemindersModal(): void {
    this.showDayRemindersModal.set(false);
    this.selectedDayReminders.set([]);
    this.selectedDayDate.set(null);
  }

  formatDateTime(dateString: string): string {
    const date = new Date(dateString);
    return date.toLocaleString();
  }

  onDayReminderUpdated(): void {
    this.loadReminders();
    // Refresh the day reminders if modal is still open
    if (this.selectedDayDate()) {
      const dayReminders = this.getRemindersForDate(this.selectedDayDate()!);
      this.selectedDayReminders.set(dayReminders);
    }
  }
}
