import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { 
  Reminder, 
  ReminderResponse, 
  CreateReminderRequest, 
  UpdateReminderRequest 
} from '../models/reminder.model';

@Injectable({
  providedIn: 'root',
})
export class ReminderService {
  private baseUrl = environment.apiBaseUrl;

  constructor(private http: HttpClient) {}

  createReminder(reminder: CreateReminderRequest): Observable<ReminderResponse> {
    return this.http.post<ReminderResponse>(`${this.baseUrl}/Reminder`, reminder);
  }

  updateReminder(reminder: UpdateReminderRequest): Observable<ReminderResponse> {
    return this.http.put<ReminderResponse>(`${this.baseUrl}/Reminder`, reminder);
  }

  deleteReminder(id: number): Observable<ReminderResponse> {
    return this.http.delete<ReminderResponse>(`${this.baseUrl}/Reminder/${id}`);
  }

  getMyReminders(): Observable<ReminderResponse> {
    return this.http.get<ReminderResponse>(`${this.baseUrl}/Reminder/my-reminders`);
  }
}
